@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');

* {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 5px;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

.calcZero {
  grid-column: 1/3;
  grid-row: 5;
}

button {
  margin: 1px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 500;
}

.highlight {
  background: orange;
}

.display {
  height: 75px;
  width: 249px;
  overflow-x: scroll;
  background-color: #101116;
  border: 0;
  color: #fff;
  text-align: right;
  font-weight: 500;
  font-size: 25px;
  letter-spacing: 1px;
}

/* Navbar CSS */
.nav {
  width: 100vw;
  min-height: 10vh;
  background-color: var(--bg-secondary);
  display: flex;
  justify-content: space-around;
  align-items: center;

}
.title {
  font-size: 4rem;
  font-weight: bolder;
  letter-spacing: 0.5rem;
  line-height: 1rem;
  color: var(--gray-clr);
  font-style: italic;
  text-shadow: 0 0.5rem 1rem var(--border-clr);
}
.link {
  list-style-type: none;
  display: flex;
  gap: 2rem;
}

.links {
  text-decoration: none;
  color: var(--white-clr);
  transition: 0.4s;
}


/* Quotes Page CSS */
.quote {
  color: white;
  font-size: 1.8rem;
  width: 70vw;
  text-align: center;
}

header {
  min-height: 10vh;
}

main {
  width: 100vw;
  min-height: 80vh;
}

.calculator-section {
  width: 100vw;
  min-height: 80vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.calculator-section h2 {
  font-size: 4rem;
  align-self: flex-start;
  margin-top: 10rem;
}

.calculator {
  width: 60%;
}